<template>
    <div class="col-12 header">
    
      <h4>
        Пользователь {{ key }}
      </h4>
      <div>
       <div class="text">
         <div class="text-1">
             <p>ФИО:</p>
             <p>Адрес:</p>
             <p>Дата рождения:</p>
             <p>Серия Номер:</p>
             
         </div>
         <div class="text-2">

         
           <p>{{ user.name }}</p>
           <p> {{user.adress}}</p> 
           <p>{{user.birthday}}</p> 
          <p> {{user.passport.seriya}} {{user.passport.number}}</p>
          
        </div>
       </div>
        <hr class="my-4">
       <div class="buttons">
          <b-btn class="edit-btn" variant="success" @click.stop="editboard(key)">Изменить данные</b-btn>
        <b-btn variant="danger" class="delete" @click.stop="deleteboard(key)">Удалить пользователя</b-btn>
       </div>
      </div>
   
  </div>
</template>
<script>
import firebase from '../Firebase'

export default {
    name: "UserComponent",
    data: () => ({
     key: '',
     user: {}
    }),
    mounted () {
    const ref = firebase.firestore().collection('users').doc(this.$route.params.id);
    ref.get().then((doc) => {
      if (doc.exists) {
        this.key = doc.id;
        this.user = doc.data();
      } else {
        alert("No such document!");
      }
    });
  },
  methods: {
      editboard (id) {
        this.$router.push({
         name: 'EditBoard',
         params: { id: id }
        })
    },
       deleteboard (id) {
      firebase.firestore().collection('users').doc(id).delete().then(() => {
        this.$router.push({
          name: 'baselayout'
        })
      }).catch((error) => {
        alert("Error removing document: ", error);
      });
    }
  }
  }

</script>

<style lang="scss">
    .header{
        margin-top: 40px;
    }
    .delete{
        margin-left: 20px;
    }
    h4{
      font-weight: 600 !important;
      padding: 0 80px;
    }
    .text{
      padding: 0 80px;
      font-size: 21px;
      display: flex;
      align-items: center;
      margin-top: 30px;
           .text-1{
             display: flex;
             flex-direction: column;
             align-items: flex-end;
             p{
               line-height: 20px;
             }
           }
           .text-2{
             margin-left: 20px;
             p{
               line-height: 20px;
             }
             display: flex;
             flex-direction: column;
             justify-content: flex-start;
             padding: 0 20px;
             font-weight: 600;
           }
    }

    .buttons{
      display: flex;
      padding: 0 80px;
      .btn{
        border-radius: unset;
      }
    }
</style>