<template>
<div class="base-layout col-12">
        <div class="logo col-12">
            <div class="left">
                <img src="/logo.png" alt="">
            <h2>Паспортный стол РФ</h2>
            </div>
            <div class="right">
                  Сотрудник {{user.data.email}}
            </div>
        </div>
         <div class="top col-12">
              <h2 class="base-layout--header">Данные клиентов</h2>
              <button 
              class="btn btn-primary"
              @click="showAddModal = true">
                 Добавить данные
              </button>
         </div>
  


    <add-form 
    class="base-layout--add-form  col-xl-3 col-lg-3 col-md-4 col-12" 
    v-if="showAddModal === true" 
    @closeModal="closeModal" 
    />

    <edit-form 
    :currentItemData="{
     currentIdx, currentItem
    }" 
    
    class="base-layout--edit-form  col-xl-3 col-lg-3 col-md-4 col-12" 
    v-if="showEditModal === true" 
    @closeEditModal="closeEditModal" 
    />

    <div class="base-layout__line">
     
        <small-details 
        @openEditModal="openEditModal"
        @click.native="details(user)"
        class="col-12 col-md-4 small-card" 
        :user="user" 
        :index="index"
        v-for="(user, index) in users" 
        :key="index" />
      


    </div>

</div>
</template>

<script>
import SmallDetails from "@/components/SmallDetails.vue"
import AddForm from "@/components/AddForm.vue"
import firebase from '../Firebase'
import {mapGetters} from 'vuex'
export default {
    name: 'BaseLayout',
    components: {
        AddForm,
        SmallDetails
    },
    computed:{
       ...mapGetters({
           token: 'token',
           user: 'user',
       }),
       
    },
    data() {
        return {
            showAddModal: false,
            alert: false,
            showEditModal: false,
            currentItem: {},
            currentIdx : Number,
            users: [],
            ref: firebase.firestore().collection('users'),
        }
    },
    created () {
      this.ref.onSnapshot((querySnapshot) => {
      this.users = [];
      querySnapshot.forEach((doc) => {
          console.log(doc.data())
        this.users.push({
          key: doc.id,
          adress: doc.data().adress,
          birthday: doc.data().birthday,
          name: doc.data().name,
          passport: {
              number: doc.data().passport.number,
              seriya: doc.data().passport.seriya,
          }
        });
      });
    });
    },
    methods: {
        closeModal() {
            this.showAddModal = false
        },
        closeEditModal() {
            this.showEditModal = false
        },
        openEditModal() {
            this.showEditModal = true;
        },
        details (user) {
         this.$router.push({ name: 'showUser', params: { id: user.key }})
        }
    },
    
    
}
</script>

