ыполняла паспортно визовая служба – были переданы Федеральной миграционной службе – ФМС России, хотя и по прежнему наименованию – остающемуся популярным, поиск адресов и телефонов продолжают разыскивать. В уточненном варианте паспортно визовая служба России – с прежним и новым названием и обозначенными подчинениями управляющих инстанций – наверху по отношению к ней, кстати, – МИД и МВД теперь располагаются, – о ее деятельности в разных городах информирует портал Таможня.Ру и его Юридическая консультация дает практические советы при обращении в нее.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"sections"},[_c('h3',[_vm._v("Разделы")]),_c('div',{staticClass:"menu-2"},[_c('a',{attrs:{"href":""}},[_vm._v("Главная")]),_c('a',{attrs:{"href":""}},[_vm._v("Паспортный стол")]),_c('a',{attrs:{"href":""}},[_vm._v("Недвижимость")]),_c('a',{attrs:{"href":""}},[_vm._v("Иммиграция")]),_c('a',{attrs:{"href":""}},[_vm._v("Работа")])])])}]

