<template>
<div class="add-form">
    <div class="add-form__close">
        <img class="add-form__close--img" src="@/assets/buttons/close.png" alt="" @click="closeModal(); " />
    </div>
    <h3 class="add-form__heading">
        Информация о пользователе
    </h3>

    <form class="add-form__form" @submit.prevent="addData(); closeModal()">

      <div class="add-form__form--wrapper">
          <label for="name">ФИО</label>
          <input type="text" id="name" placeholder="ФИО" v-model="name" >
     </div>
     <div class="add-form__form--wrapper">
          <label for="birthday">Дата рождения</label>
          <input type="date" id="birthday" placeholder="Дата рождения" v-model="birthday" >
     </div>
     <div class="add-form__form--wrapper">
          <label for="birthday">Паспорт</label>
          <input type="text"  placeholder="Серия" v-model="passport.seriya" >
          <input type="text"  placeholder="Номер" v-model="passport.number" >
     </div>
     <div class="add-form__form--wrapper">
          <label for="birthday">Адрес прописки</label>
          <input type="text"  placeholder="Адрес" v-model="adress" >
     </div>

        <button type="submit" class="add-form__form--button">
            Добавить
        </button>
    </form>
</div>
</template>

<script>
import firebase from '../Firebase'
export default {
    name: 'AddForm',

    data() {
        return {
           
            name: '',
            birthday: '',
            passport: {
                seriya: '',
                number: ''
            },
            adress: ''
          
        }
    },

    methods: {
        closeModal() {
            this.$emit("closeModal")
        },
        addData() {
        
        var data =  {
                name:this.name, 
                birthday: this.birthday,
                passport: {
                    seriya: this.passport.seriya,
                    number: this.passport.number
                },
                adress: this.adress
            };

         firebase.firestore().collection('users').add(data).then(() => {
          this.$router.push({
          name: 'baselayout'
          })
         })

        }
    },
}
</script>

<style lang="scss" scoped>
.add-form {
    min-height: 100vh;
    padding: 30px;

    &__heading {
        font-weight: 600;
        font-size: 25px;
        margin-top: 40px;
        border-bottom: 1px solid rgb(226, 226, 226);
        padding-bottom: 20px;

    }

    &__close {
        position: absolute;
        right: 15px;
        top: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 3px;

        &--img {
            width: 17px;
            height: 17px;
        }

        &:hover {
            cursor: pointer
        }
    }

    &__form {
        display: flex;
        flex-direction: column;
        margin-top: 40px;


        &--button {
            box-shadow: 3px 2px 6px 0px rgba(0, 0, 0, 0.13), -3px -2px 6px 0px rgb(255, 255, 255);
            border: unset;
            padding: 10px 15px;
            font-weight: 500;
            transition: 0.2s;
            margin-top: 20px;
            background: rgb(235, 134, 2);
            color: white;

            &:focus {
                outline: none;
            }

            &:hover {
                cursor: pointer;
                box-shadow: 3px 2px 4px -1px rgba(0, 0, 0, 0.4), -3px -2px 6px 0px rgb(255, 255, 255);
            }

            
        }
        &--wrapper {
            display: flex;
            flex-direction: column;
            margin-top: 20px;

            label{
                line-height: 14px;
                font-size: 14px;
            }       
            input{
                padding: 5px 10px;
                border:1px solid rgba(128, 128, 128, 0.555);
                background: white;
            }     

            &-disable-input {
                outline: none;
                border: none;
                background: transparent;
                width: min-content;
                text-align: right;
                font-weight: 500;
                font-size: 20px;
                color: rgb(235, 134, 2);
                width: 50%;
            }
        }
    }
}
</style>
