<template>
    <b-col cols="12">
      <h2>
        Изменение данных пользователя<br>
        
      </h2>
      <router-link :to="{ name: 'baselayout' }">На главную</router-link>
      <b-jumbotron>
        <b-form @submit="onSubmit">
          <b-form-group id="fieldsetHorizontal"
                    horizontal
                    :label-cols="4"
                    breakpoint="md"
                    label="Адрес*">
            <b-form-input id="title" v-model.trim="user.adress"></b-form-input>
          </b-form-group>
          <b-form-group id="fieldsetHorizontal"
                    horizontal
                    :label-cols="4"
                    breakpoint="md"
                    label="Дата рождения*">
            <b-form-input id="date" v-model.trim="user.birthday"></b-form-input>
          </b-form-group>
          <b-form-group id="fieldsetHorizontal"
                    horizontal
                    :label-cols="4"
                    breakpoint="md"
                    label="Серия и номер паспорта*">
            <b-form-input id="number" v-model.trim="user.passport.number"></b-form-input>
            <b-form-input id="number" v-model.trim="user.passport.seriya"></b-form-input>
          </b-form-group>
          <b-form-group id="fieldsetHorizontal"
                    horizontal
                    :label-cols="4"
                    breakpoint="md"
                    label="ФИО*">
            <b-form-input id="author" v-model.trim="user.name"></b-form-input>
          </b-form-group>
          <b-button type="submit" variant="primary">Обновить</b-button>
        </b-form>
      </b-jumbotron>
    </b-col>
</template>

<script>

import firebase from '../Firebase'
import router from '../router'

export default {
  name: 'EditUser',
  data () {
    return {
      key: this.$route.params.id,
      user: {}
    }
  },
  created () {
    const ref = firebase.firestore().collection('users').doc(this.$route.params.id);
    ref.get().then((doc) => {
      if (doc.exists) {
        this.user = doc.data();
      } else {
        alert("No such document!");
      }
    });
  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()
      const updateRef = firebase.firestore().collection('users').doc(this.$route.params.id);
      updateRef.set(this.user, {merge: true}).then(() => {
        router.push({ name: 'baselayout' })
      },
      )
      .catch((error) => {
        alert("Error adding document: ", error);
      });
    }
  }
}
</script>

<style lang="scss" scoped>
  h2{
    font-weight: 600 !important;
  }
  .col-form-label{
    text-align: right !important;
  }
  .jumbotron{
    margin-top: 40px;
  }
  .btn{
    border-radius: unset;
  }
</style>