import Vue from 'vue'
import Vuex from 'vuex'
import router from '@/router'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
     token: false,
     user: {
      data: null
     },
  },
  mutations: {   
    SET_TOKEN: async(state) => {
      state.token = true;
      router.push('/admin');
    },
    AUTH_TOKEN: async(state) => {
      state.token = true;
    },
    DELETE_TOKEN: async(state) => {
      state.token = false
    },
    SET_USER(state, data) {
      state.user.data = data;
    },
  },
  actions: {
    AUTH_USER: async({commit}) => {
      commit('SET_TOKEN');

    },
    OUT_USER: async({commit}) => {
      commit('DELETE_TOKEN')
    },
    fetchUser: async ({ commit }, user) => {
      if (user) {
        await commit("SET_USER", {
          email: user.email,
          uid: user.uid
        });
        await commit('SET_TOKEN');
      } else {
        commit("SET_USER", null);
      }
    }
  },
  getters: {
    token: (state) =>  state.token,
    user: (state) => state.user,
  }
})
